<template>
  <div class="tabbar-wrapper" ref="tabbar" id="tabbar" ontouchmove="event.preventDefault()">
    <div class="back"  ><i v-show="path!='/' && path!='/Login'" @touchend="back" class="fa fa-angle-left fa-2x"></i></div>
      心心画板
    <div class="else"><i v-show="path==='/'" @touchend="clear" class="fa fa-trash-o"></i></div>
  </div>
</template>

<script>
import {clearCache} from "../js/cache"
export default {
  name: 'Tabbar',
  data () {
    return {
      path: "/"
    }
  },
  watch: {
    '$route'(to, from){
      this.path = to.path
    }
  },
  methods: {
    back(){
      this.$router.go(-1)
    },
    clear(){
      var c = confirm("是否清楚缓存")
      if(c) {
        clearCache()
      }
    }
  },
  mounted() {
    
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.tabbar-wrapper{
  height: 44px;
  line-height: 44px;
  text-align: center;
  box-shadow: 0px 5px 16px 4px rgba(161,161,161,0.1);
  font-weight: bold;
  z-index: 999999;
  font-size: 16px;
  // position: fixed;
  left: 0;
  top: 0;
  width: 100%
}
.back{
  float: left;
  height: 100%;
  width: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  // background-color: #000
}
.else{
  float: right;
  height: 100%;
  width: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  // background-color: #000
}
</style>



