<template>
  <div class="login-page">
    <div class="content">
      <div class="logo">
        <img style="width:125px" src="../assets/images/Loginlogo.png" alt="" srcset="">
        <img src="../assets/images/login_text.png" alt="" srcset="">
      </div>
      <div class="input-wrapper">
        <input type="text" v-model="name" placeholder="你的姓名">
        <input type="text" v-model="school" placeholder="所在学校">
        <input type="text" v-model="className" placeholder="所在班级">
      </div>
      <div class="loginbtn" @touchend="login">开始我的画画之旅</div>
    </div>
  </div>
</template>
<script>
import {getCache,setCache,INFO} from "../js/cache"
export default {
  data() {
    return {
      name:"",
      school:"",
      className:""
    }
  },
  methods: {
    login(){
      if(this.name == ""){
        this.$toast.center("请输入姓名");
        return
      }
      if(this.school == ""){
        this.$toast.center("请输入所在学校")
        return
      }
      if(this.className == ""){
        this.$toast.center("请输入所在班级")
        return
      }
      setCache(INFO, {
        name: this.name,
        school: this.school,
        className: this.className
      })
      this.$router.push("/")
    }
  },
  mounted() {
    
  },
};
</script>

<style lang="less" scope>
.login-page {
  height: 100%;
  background-color: #fcf7d2;
  display: flex;
  padding-top: 30px;
  .content {
    margin: 0 auto;
    width: 250px;
    .logo {
      font-size: 0;
      text-align: center;
      img {
        width: 100%;
      }
    }
    .input-wrapper {
      input {
        color:  #f4c06b;
        border: 2px solid #f4c06b;
        line-height: 85/2px;
        box-sizing: border-box;
        border-radius: 85/4px;
        background-color: rgba(0,0,0,0);
        width: 100%;
        margin-bottom: 20px;
        padding: 0 15px;
        font-size: 14px;
        text-align: center
      }
    }
    .loginbtn{
        width: 100%;
        line-height: 85/2px;
        background-image: url("../assets/images/btn.png");
        background-size: 100% 100%;
        background-color: none;
        text-align: center;
        color: #fff;
        margin-top: 10px;

    }
  }
  input::-webkit-input-placeholder {
    color: #f7d26d;
    text-align: center
  }
  input:-moz-placeholder {
      color: #f7d26d;
      text-align: center
  }
  input:-ms-input-placeholder {
      color: #f7d26d;
      text-align: center
  }

}
</style>



